/* Windows 98 Style Dropdown */
select {
  font-family: "MS Sans Serif", Tahoma, sans-serif;
  font-size: 12px;
  background-color: #c0c0c0; /* classic gray background */
  border: 1px solid black;
  border-radius: 0;
  padding: 2px 4px;
  color: black;
  appearance: none;
  -webkit-appearance: none;
  -moz-appearance: none;
  width: 200px; /* adjust as per your requirement */
}

/* Add a focus effect to the dropdown */
select:focus {
  outline: none;
  box-shadow: 0 0 0 1px black;
}

/* Option styling */
select option {
  background-color: #c0c0c0; /* classic gray background */
  color: black;
}

/* For the 3D effect */
select:active, select:hover {
  border-color: white black black white;
}

.button-nostalgia {
  font-family: "MS Sans Serif", Tahoma, sans-serif;
  font-size: 12px;
  background-color: #c0c0c0; /* classic gray background */
  border: 2px outset #c0c0c0;
  padding: 4px 8px;
  color: black;
  cursor: pointer;
  border-radius: 0; /* No rounded corners */
  text-align: center;
  text-decoration: none;
  display: inline-block;
  transition: background-color 0.2s, border 0.2s; /* Smooth transition for hover and active states */
}

/* Hover effect to highlight the button */
.button-nostalgia:hover {
  background-color: #e0e0e0;
}

/* Active state to give the appearance of the button being pressed */
.button-nostalgia:active {
  border: 2px inset #c0c0c0;
}

