@-webkit-keyframes fadeInOutAnimation {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes fadeInOutAnimation {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.fadeInOut {
  cursor: pointer;
  -webkit-animation: fadeInOutAnimation 1.5s infinite;
          animation: fadeInOutAnimation 1.5s infinite;
  animation-direction: alternate-reverse;
  -webkit-animation-timing-function: ease-in-out;
          animation-timing-function: ease-in-out;
}
.fastFadeInOut {
  -webkit-animation: fadeInOutAnimation 0.5s infinite;
          animation: fadeInOutAnimation 0.5s infinite;
  animation-direction: alternate-reverse;
  -webkit-animation-timing-function: ease-in-out;
          animation-timing-function: ease-in-out;
}
.fadeIn {
  opacity: 0;
  cursor: pointer;
  transition: 0.25s all ease-in-out;
}

@-webkit-keyframes slide {
  0% { left: 5px; }
  70% { left: -25px; }
  100% { left: -25px; top: 10px; }
}

@keyframes slide {
  0% { left: 5px; }
  70% { left: -25px; }
  100% { left: -25px; top: 10px; }
}
