/* just a reset of the css */
body {
    position: absolute;
    height: 100%;
    width: 100%;
    margin: 0;
    padding: 0;
    font-family: 'Helvetica';
  }
  .main-container {
    width: 100%;
    height: 100%;
    display: flex;
    margin: 0;
    justify-content: center;
    align-items: center;
    background-color: #fafafa;
  }
  
  .table-container {
    display: flex;
    flex-flow: column nowrap;
    background-color: white;
    width: 100%;
    margin: 0 auto;
    border-radius: 4px;
    border: 1px solid #DADADA;
    box-shadow: 0px 1px 4px rgba(0, 0, 0, .08);
  }
  
  .table-row {
    display: flex;
    flex-flow: row nowrap;
    width: 100%;
    border-bottom: 1px solid #dadada;
  }
  
  .heading {
    background-color: #ececec;
    color: #3e3e3e;
    font-weight: bold;
  }
  
  .row-item {
    display: flex;
    flex: 1;
    font-size: 14px;
    padding: 8px 0;
    justify-content: center;
    align-items: center;
    transition: all 0.15s ease-in-out;
  }
  
  .row-item:hover {
    cursor: pointer;
    background-color: #F0F0F0;
  /*   box-shadow: 0px 1px 4px rgba(0, 0, 0, .08); */
  }
  
  .row-sub-container {
    display: flex;
    flex-flow: column nowrap;
    flex: 1;
  }
  
  .row-sub-container .row-item {
    padding: 8px 0;
    border-bottom: 1px solid #dadada;
  }
  
  .table-row:last-child,
  .row-sub-container .row-item:last-child {
    border-bottom: 0;
  }